<template>
  <div class="answer-warp">
    <Header>
      <router-link to="/goodsAnswerMine" slot="right">我的问答</router-link>
    </Header>
    <GoodsInfo :sku="sku" ref="goodsInfo"/>
    <div class="answer-search">
      <img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/goods/answer/search.png" alt="">
      <input type="text" placeholder="搜索已有问题" v-model="searchText" maxlength="40" @input="doSearch">
    </div>
    <div class="answer-list" v-if="list.length">
      <van-list
          class="answer-list"
            v-model="loading"
            :finished="finished"
            finished-text="没有更多了"
            @load="onLoad"
          >
        <div class="answer-item" v-for="item in list" :key="item.questionId" @click="goDetail(item)">
          <h4><img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/goods/answer/question.png" alt=""><p>{{item.question}}</p></h4>
          <div v-if="item.bestAnswer">
            <img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/goods/answer/answer.png" alt="">
            <p>{{item.bestAnswer}}</p>
          </div>
          <div v-else>
            <img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/goods/answer/answer_disable.png" alt="">
            <p>暂无回答</p>
          </div>
          <a href="javascript:;" v-if="item.answerCount">全部{{item.answerCount}}个回答 <van-icon name="arrow" size=".26rem" color="#2878FF"/></a>
        </div>
      </van-list>
    </div>
    <AnswerEmpty v-if="!list.length && finished"/>
    <div class="answer-add-btn">
      <router-link :to="`/goodsAnswerAdd?sku=${sku}`">我要提问</router-link>
    </div>
  </div>
</template>

<script>
import Header from '@/components/header'
import GoodsInfo from './component/goods-info'
import AnswerEmpty from './component/answer-empty'
import { getPageProductQuestion } from '@/http/service/goods'
import { mapMutations } from 'vuex'
export default {
  name: 'answerList',
  components: {
    Header,
    GoodsInfo,
    AnswerEmpty
  },
  data() {
    return {
      sku: '',
      rowsCount: '',
      timer: null,
      curPage: 1,
      pageSize: 20,
      searchText: '',
      list: [],
      loading: false,
      finished: false,
    }
  },
  activated () {
    console.log('========')
    console.log(this.getAnswerInfo, 'getAnswerInfo')
    this.sku = this.$route.query.sku
    this.init();
    if (this.sku && this.$refs.goodsInfo) this.$refs.goodsInfo.getProductQuestionInfo()
  },
  methods: {
    ...mapMutations('answerArea', ['setQuestionInfo']),
    init() {
      this.curPage = 1;
      this.list = [];
      this.finished = false
      this.loading = false
      this.searchText = '';
      this.getPageProductQuestion();
    },
    onLoad() {
      this.getPageProductQuestion();
    },
    doSearch() {
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        clearTimeout(this.timer);
        this.list = []
        this.curPage = 1;
        this.loading = false;
        this.finished = false;
        this.getPageProductQuestion();
      }, 500)
    },
    getPageProductQuestion() {
      this.loading = true
      let params = {
        start: this.curPage,
        offset: this.pageSize,
        sku: this.sku,
      }

      if (this.searchText) params.question = this.searchText

      getPageProductQuestion(params).then(res => {
        let { result, success, pager } = res;
        if (success) {
          this.rowsCount = pager.rowsCount
          this.curPage += 1;
          this.loading = false
          this.list = [...this.list, ...result]
          this.finished = result.length < this.pageSize
        } else {
          this.loading = false
          this.finished = true
        }
      })
    },
    goDetail(item) {
      this.setQuestionInfo(item)
      this.$router.push('/goodsAnswerDetail/' + item.questionId)
    }
  }
}
</script>

